<template>
	<div class="details_box">
		<!--头部-->
		<header>
			<div class="details_head">
				<p>商品</p><p>评价</p>
			</div>
		</header>
		<!--内容-->
		<div class="details_cont">
			<!--获取左侧导航栏-->
			<div class="details_left">
				<p @click="showRight(index)" v-for="(item,index) in list" :key="index">{{item.name}}</p>
			</div>
			<!--获取右侧内容-->
			<div class="details_right">
				<ul>
					<!--渲染右侧头部-->
					<li v-for="(val,key) in arr" :key="key" v-show="key==num">
						<p class="details_nav"><span>{{val.name}}</span><span>{{val.description}}</span></p>
						<!--获取右侧内容-->
						<div class="details_text" v-for="(i,k) in val.foods" :key="k">
							<p><img :src="`//elm.cangdu.org/img/`+i.image_path" alt=""></p>
							<div class="details_content">
								<p class="details_title">{{i.name}}</p>
								<p>{{i.description}}</p>
								<p><span>{{i.tips}}</span><span>好评率{{i.satisfy_rate}}%</span></p>
								<!--渲染价格数组截取-->
								<div  class="details_addShop">
									<div class="price" v-for="(itm,i) in i.specfoods.slice(i,1)" :key="i">￥{{itm.price}}</div>
									<div class="addShop">
										<p><button v-show="i.is_essential" @click="del(val,k)">-</button></p>
										<span v-show="i.is_essential">{{i.is_featured}}</span>
										<p><button @click="addShop(val,k,$event)">+</button></p>
									</div>
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
		<!--底部-->
		<footer>
			<DetailsFoot :start="start"></DetailsFoot>
		</footer>
	</div>
</template>

<script>
	import theBall from '../../../plugins/theBall'
	import DetailsFoot from './detailsFoot'
	export default {
		name: "cartDetails",
		components:{
			DetailsFoot
		},
		data(){
			return{
				list:[],
				arr:[],
				num:0,
				start:true,
			}
		},
		//方法
		methods:{
			//异步请求数据
			async getData(){
			await this.$axios.get("http://elm.cangdu.org/shopping/v2/menu?restaurant_id="+this.$route.query.cartdetails).then(res =>{
					this.list = res.data;
					this.arr = res.data;
				})
			},
			//点击左侧导航显示右侧内容用下标
			showRight(index){
				this.num =index
			},
			//点击添加商品
			addShop(val,k,e){
				theBall(e)
				val.foods[k].is_featured++
				val.foods[k].is_essential=true
				this.$store.commit('addShop',val.foods[k])
			},
			del(val,k){
					val.foods[k].is_featured--
				if(val.foods[k].is_featured==0){
					val.foods[k].is_essential=false
				}
			}
		},
		//生命周期页面加载完毕DOM已经渲染在页面中
		created(){
			this.getData()
		},
	}
</script>
