<template>
	<div class="details_foot">
		<div v-if="this.arr.length==0" class="details_one">
			<div class="details_price">
				<i><van-icon class="active" name="shopping-cart" /></i>
				<div>
					<p>0.00</p>
					<p>配送费￥5元</p>
				</div>
			</div>
			<div><button class="details_btn">还差￥20元起送</button></div>
		</div>
		<div v-else class="details_two">
			<div class="details_price">
				<i><van-icon @click="show" class="active_two sc" name="shopping-cart" /></i>
				<div>
					<p>￥{{this.allPrice}}</p>
					<p>配送费￥5元</p>
				</div>
			</div>
			<p class="allNum">{{this.allNum}}</p>
			<router-link to="/goods">
				<van-button type="primary" class="van_btn">去结算</van-button>
			</router-link>
		</div>
		<div class="details_show" v-show="flag">
			<div class="details_cart">
				<p>购物车</p>
				<p @click="del">清空</p>
			</div>
			<ul class="details_ul">
				<li v-for="(item,index) in arr" :key="index">
					<div class="details_num">
						<p>{{item.name}}</p>
						<p v-for="(val,key) in item.specfoods.slice(0,1)" :key="key">￥{{val.price}}</p>
					</div>
					<div>
						<button @click="fnJian(index)">-</button>
						<span>{{item.num}}</span>
						<button @click="fnJia(index)">+</button>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import {mapState,mapMutations} from 'vuex'
	import taeBass from '../../../plugins/theBall.css'
	export default {
		name: "detailsFoot",
		props:['start'],
		methods:{
			...mapMutations(['getShop','show','del']),
			fnJia(index){
				this.$store.commit('fnAdd',index)
			},
			fnJian(index){
				this.$store.commit('fnPrev',index)
			},
		},
		computed:{
			...mapState(['arr','flag']),
			allPrice(){
				let data = [...this.arr]
					let Allprice = 0
				for(let i=0;i<data.length;i++){
					for(let j=0;j<data[i].specfoods.length;j++){
						Allprice+=data[i].num*data[i].specfoods[j].price
					}
				}
				return Allprice
			},
			allNum(){
				let data = [...this.arr]
				let Allnum = 0
				for(let i=0;i<data.length;i++){
					Allnum+=data[i].num
				}
				return Allnum
			}
		},
		created(){
			this.getShop()
		},
		beforeUpdate(){
			this.getShop()
		}
	}
</script>

